export default {
    namespaced: true,
    state: {
        usermame: '',
        id: '',
        isLoggedIn: false,
        avatarId: '',
        elo: 0
      },
      mutations: {
        SET_USER: function (state, payload) {
          state.id = payload._id;
          state.avatarId = payload.avatarId;
          state.username = payload.username;
          state.rank = payload.rank;
          state.elo = payload.elo || 0; 
          state.isLoggedIn = true;
        }
      },
      getters: {
        getUserData: (state)=>{
          return {
            id: state.id,
            avatarId: state.avatarId, 
            username: state.username,
            rank: state.rank,
            wins: 0,
            losses: 0,
            streak: 0
          }
        },
        ranked:(state)=>{
          return {elo: state.elo, pid: state.id}
        },
      },
      actions: {
      },
}